/* -----------nav--------------- */
nav {
    .menu {
        display: flex;
        flex-flow: row;
        font-size: 13px;
        font-weight: 600;
        transform: translateX(-15px);
        li {
            a {
                span {
                    font-weight: 400;
                    position: absolute;
                }
            }
            ul.menu_1 {
                display: none;
            }
            &:hover {
                ul.menu_1 {
                    display: block;
                }
                ul.last_menu {
                    position: absolute;
                    right: 0;
                    width: 165%;
                    text-align: left;
                    display: flex;
                    flex-direction: row;
                    background-color: #fff;
                    font-weight: 400;
                    transition: .4s;
                }
            }
        }
    }
}
nav .menu>li { text-align: center; }
nav .menu>li:nth-child(1) { width: 112px; }
nav .menu>li:nth-child(2) { width: 107px; }
nav .menu>li:nth-child(3) { width: 107px; }
nav .menu>li:nth-child(4) { width: 103px; }
nav .menu>li:nth-child(5) { width: 116px; }
nav .menu>li:nth-child(6) { 
    width: 135px; 
}
.last_menu>li {
    width: 100%;
}
.menu_2_2 {
    display: flex;
    flex-flow: column;
    padding: 20px;
    width: 100%;
}

.menu_2_2>li>a {
    display: block;
    padding: 10px 12px;
    font-size: 12px;
    width: 100%;
}

.menu_2_2>li:hover>a {
    background-color: #d6b161;
    color: #fff;
    transition: .4s;
}

nav .menu>li>a {
    height: 100px;
    line-height: 100px;
    padding: 0 12px;
    color: #fff;
    position: relative;
}
nav .menu>li> ul {
    position: absolute;
    top: 100px;
    background-color: #fff;
    width: 190px;
    text-align: left;
    font-size: 11px;
    font-weight: 400;
}
nav .menu>li> ul>li>a {
    padding: 12px 20px;
    color: #000;
    font-weight: 400;
}
nav .menu>li> ul>li:hover>a {
    background-color: #d6b161;
    color: #fff;
    transition: .4s;
}

nav .menu>li:hover>a {
    color: #d6b161;
    transition: .2s;
} 

 nav .menu>li> ul:nth-child(2)>li {
    position: relative;
    transition: .4s;
}
 nav .menu>li> ul:nth-child(2)>li:first-child strong {
    margin-left: 94px;
    font-weight: 700;
    font-size: 16px;
    transition: .4s;
}
 nav .menu_2_1 {
    position: absolute;
    top: 0; left: 190px;
    width: 190px;
}
 nav .menu2 li:hover .menu_2_1 a {
    background-color: #fff;
    color: #000;
    padding: 10px 0px 10px 22px;
    transition: .4s;
}
 nav .menu2 li:hover .menu_2_1 li:hover a {
    background-color:  #d6b161;
    color: #fff;
    transition: .4s;
}
 nav .menu2>li ul {
    display: none;
}
 nav .menu2>li:hover> ul {
    display: block;
    transition: .4s;
}
/* ----------HeaderHead1---------------- */
.header_box1 {
    text-align: center;
    position: absolute; 
    top: 55%; left: 50%; transform: translate(-50%, -50%);
    h2 {
        font-size: 60px;
        font-family: 'Source Sans Pro', sans-serif;
        font-weight: 300;
        color: #333333;
        word-spacing: -1px;
        letter-spacing: 6px;
        animation: box1h2 5.5s ease 0s infinite;
    }
    h3 {
        color: #555555;
        font-size: 20px;
        font-family: 'gotham','NanumSquare', 돋움, sans-serif;
        margin-top: 20px;
        letter-spacing: 5px;
        word-spacing: 1px;
        line-height: 35px;
        font-weight: 500;
        animation: box1h3 5.5s ease 0s infinite;
    }
    .header_checkbox {
        margin: 50px auto;
        width: 191px;
        box-sizing: border-box;
        transform: translateX(-12px);
        animation: box1a 5.5s ease 0s infinite;
        a {
            font-family: 'gotham','NanumSquare', 돋움, sans-serif;
            height: 52px;
            border: .5px solid #111;
            display: block;
            color: #333;
            padding: 13px 25px;
            font-size: 14px;
            font-weight: 500;
            letter-spacing: 1px;
            img {
                width: 17px;
                height: 17px;
                transform: translateY(3px);
                filter: invert(34%) sepia(0%) saturate(0%) hue-rotate(171deg) brightness(91%) contrast(89%);
            }
        }
        &:hover {
            a {
                background-color: #222;
                color: #fff;
                font-weight: 600;
                transition: .4s;
                img {
                    filter: invert(100%) sepia(0%) saturate(7495%) hue-rotate(293deg) brightness(104%) contrast(103%);
                }
            }
        }
    }
}
