/* ---------------box1---------------- */
@keyframes box1h2{
    0% {
      opacity: 0;
      transform: translateY(-400px);
    }
    15% {
        opacity: 1;
        transform: none;
    }
    30% {
        opacity: 1;
        transform: none;
    }
    50% {
        opacity: 1;
        transform: none;
    }
    65% {
        opacity: 1;
        transform: none;
    }
    80% {
        opacity: 0;
        transform: translateY(-100px);
    }
    100% {
        opacity: 0;
        transform: translateY(-100px);
    }
  
}
@keyframes box1h3{
    0% {
        opacity: 0;
        transform: translateY(400px);
    }
    15% {
        opacity: 0;
        transform: translateY(400px);
    }
    30% {
        opacity: 1;
        transform: none;
    }
    50% {
        opacity: 1;
        transform: none;
    }
    70% {
        opacity: 1;
        transform: none;
    }
    88% {
        opacity: 0;
        transform: translateY(100px);
    }
    100% {
        opacity: 0;
    }
  
}
@keyframes box1a{
    0% {
        opacity: 0;
        transform: translateX(-400px);
    }
    30% {
        opacity: 0;
        transform: translateX(-400px);
    }
    50% {
        opacity: 1;
        transform: translateX(-12px);
    }
    60% {
        opacity: 1;
        transform: translateX(-12px);
    }
    75% {
        opacity: 1;
        transform: translateX(-12px);
    }
    97% {
        opacity: 0;
        transform: translateX(-100px);
    }
    100% {
        opacity: 0;
    }
  
}
/* -----------box2---------------- */
@keyframes box2h2{
    0% {
      opacity: 0;
      transform: rotateX(90deg);
    }

    30% {
        opacity: 1;
        transform: rotateX(0deg);
    }
    50% {
        opacity: 1;
        transform: rotateX(0deg);
    }
    65% {
        opacity: 1;
        transform: rotateX(0deg);
    }
    80% {
        opacity: 0;
        transform: rotateX(90deg);
    }
    100% {
        opacity: 0;
    }
  
}
