    letter-spacing: 1px;
}
main .main_foot .foot_pro, .foot_posts, .foot_link, .foot_work {
    padding: 0 30px;
}
main .main_foot .foot_posts li, .foot_link li {
    border-bottom: 1px solid #333;
}
main .main_foot .foot_posts a, .foot_link a {
    font-size: 15px;
    color: #a2a2a2;
    display: block;
    line-height: 45px;
    text-transform: none;
}
main .main_foot .foot_posts a::before, .foot_link a::before {
    padding-right: 15px;
    content: "\f105";
    font-family: "FontAwesome";
}
main .main_foot .foot_posts a:hover, .foot_link a:hover {
    color: #d6b161;
}
main .main_foot .foot_pro a {
    margin-bottom: 20px;
}
main .main_foot .foot_pro p {
    color: #7e7e7e;
    font-size: 15px;
    margin-bottom: 30px;
    line-height: 30px;
    text-transform: none;
    text-align: left;
    font-weight: 400;
}

/* footer */
footer .footer_box {
    box-sizing: border-box;
    height: 140px;
    background-color: #1b1b1b;
    color: #6f7273;
    font-size: 15px;
    padding: 35px 0;
    border-top: 1px solid #333;
    border-bottom: 5px solid #222;
}
footer .footer_box .box_grid {
    display: grid;
    grid-template-columns: repeat(2, 585px);
    justify-content: center;
    align-items: center;
}
footer .footer_box .box_grid .footer_txt {
    padding: 0 30px;
    text-transform: none;
    line-height: 1.5;
}
footer .footer_box .box_grid .footer_txt a {
    margin-left: 5px;
    display : inline-block;
    color: #fff;
}
footer .footer_box .box_grid .footer_txt a:hover {
    color: #d6b161;
}
footer .footer_box .box_grid .footer_txt span {
    margin-top: 10px;
}
footer .footer_box .footer_link {
    padding: 0 30px;
    text-align: right;
}
footer .footer_box .footer_link .link_grid {
    display: flex;
    justify-content: right;
}
footer .footer_box .footer_link .link_grid a {
    display: block;
    margin-left: 15px;
    width: 35px;
    height: 35px;
    text-align: center;
    border: 1px solid #5a5a5a;
    border-radius: 50%;
    color: #5a5a5a;
    font-size: 20px;
    line-height: 35px;
}
footer .footer_box .footer_link .link_grid a:hover {
    color: #fff;
    background-color: #5a5a5a;
}
footer .footer_box .footer_link .link_grid .fa {
    display: inline-block;
    font: normal normal normal 14px/1 FontAwesome;
    font-size: inherit;
    text-rendering: auto;
    -webkit-font-smoothing: antialiased;
    transform: translate(0, 0);
}
footer .footer_box .footer_link .link_grid .fa-facebook::before {
    content: "\f09a";
    font-family: "fontawesome";
}
footer .footer_box .footer_link .link_grid .fa-twitter::before {
    content: "\f099";
    font-family: "fontawesome";
}
footer .footer_box .footer_link .link_grid .fa-google::before {
    content: "\f0d5";
    font-family: "fontawesome";
}
footer .footer_box .footer_link .link_grid .fa-behance::before {
    content: "\f1b4";
    font-family: "fontawesome";
}
footer .footer_Up .fa {
    display: inline-block;
    font: normal;
    font-size: inherit;
    text-rendering: auto;
    /* -webkit-font-smoothing: antialiased; */
    transform: translate(0, 0);
}

footer .footer_Up .fa-up {
    position: fixed;
    right: 20px;
    bottom: 20px;
    width: 40px;
    height: 40px;
    transition: all .3s;
    text-align: center;
    color: #fff;
    background: rgba(0, 0, 0, .6);
    line-height: 40px;
}

footer .footer_Up .fa-up::before {
    content: "\f106";
    font-family: "fontawesome";
}
footer .footer_Up a:hover .fa {
    background-color: #d6b161;
    color: #7e7e7e;
}

